@import url(https://fonts.googleapis.com/css2?family=Arimo);
@import url(https://fonts.googleapis.com/css2?family=Fredoka+One);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700);
h1 {
    margin: 0px;
    color: #1ED760;
    text-shadow: 2px 0 0 #000, 0 -2px 0 #000, 0 2px 0 #000, -2px 0 0 #000;
}

h2 {
  text-shadow: 2px 0 0 #000, 0 -2px 0 #000, 0 2px 0 #000, -2px 0 0 #000;
}

.user-name {
    font-size: 60px;
    margin-bottom: 20px;
}

#user-profile-pic {
    border-radius: 10px;
    border: 4px solid #1ED760;
}

#user-profile-pic:hover {
    border: 4px solid #000;
    cursor: pointer;
}

.home-display {
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.search-input {
    padding: 5px 10px;
    border-radius:10px;
    font-family: "Montserrat";
    font-size: 20px;
    font-weight: 600;
    border: 2px solid #000;
}

.search-input:focus {
    outline: none;
}

.search-input:hover {
    border: 2px solid #b3b3b3;
}

.artist-grid {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    font-family: "Montserrat";
    color: white;
}

.artist-grid h1 {
    margin-top: 10px;
    color: white;
    cursor: pointer;
}

.artist-grid a {
    color: white;
    text-decoration: none;
    
}

a:link, a:visited {
    color: white;
}

.artist-grid h1:hover {
    text-shadow: 0 0 10px #FFFFFF, 2px 2px 2px rgba(206,89,55,0);
}

.artist-grid img {
    border-radius: 5px;
    border: 4px solid #1ED760;
}

.artist-grid img:hover {
    border: 4px solid #000;
    cursor: pointer;
}

.artist-panel {
    flex: 0 0 33.3333%;
    margin-bottom: 30px;
    overflow: hidden;
    max-width: 20%;
}

.artist-panel h2 {
  font-size: 2vw;
}

.game-interface {
    display: flex;
    justify-content: space-evenly;
    align-content: space-evenly;
    flex-wrap: wrap;
    height: 100%;
    width: 80%;
    /* margin-top: 40%; */
}

.game-btn {
    flex: 0 0 35%;
    height: 40%;
    background-color: #14a045;
    border-radius: 20px;
    font-family: "Montserrat";
    font-size: 2.5vh;
    color: white;
    text-shadow: 0 0 10px #FFFFFF, 2px 2px 2px rgba(206,89,55,0);
    outline: none;
}

.game-btn-correct {
  background-color: rgb(0, 155, 33);
}

.game-btn-incorrect {
  background-color: red;
}

.album-cover {
    width: 400px;
    height: 400px;
    /* border: 3px solid white; */
    border-radius: 20px;
    margin-top: 4%;
}

.lds-roller {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 80%;
    margin-right: 4%;
  }
  .lds-roller div {
    -webkit-animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #fff;
    margin: -4px 0 0 -4px;
  }
  .lds-roller div:nth-child(1) {
    -webkit-animation-delay: -0.036s;
            animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .lds-roller div:nth-child(2) {
    -webkit-animation-delay: -0.072s;
            animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .lds-roller div:nth-child(3) {
    -webkit-animation-delay: -0.108s;
            animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .lds-roller div:nth-child(4) {
    -webkit-animation-delay: -0.144s;
            animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .lds-roller div:nth-child(5) {
    -webkit-animation-delay: -0.18s;
            animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .lds-roller div:nth-child(6) {
    -webkit-animation-delay: -0.216s;
            animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .lds-roller div:nth-child(7) {
    -webkit-animation-delay: -0.252s;
            animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .lds-roller div:nth-child(8) {
    -webkit-animation-delay: -0.288s;
            animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @-webkit-keyframes lds-roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes lds-roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  

.recommended-artists {
  padding-top: 2%;
}
#root {
    height: 100%;
}

html {
    display: flex;
    height: 100%;
    width: 100%;
    background-color: #3D3D3D;
    background-image: linear-gradient(to bottom, #808080, rgb(22, 70, 38));
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: "Fredoka One";
}

body {
    height: 100%;
    width: 100%;
    margin: 0;
}

.login-page {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: -5%;
}

.banner {
    display: flex;
    align-items: center;
}

.banner-text {
    font-size: 80px;
    /* margin-bottom: 10px; */
    color: #1ED760;
    text-shadow: 2px 0 0 #000, 0 -2px 0 #000, 0 2px 0 #000, -2px 0 0 #000;
}

.login-btn {
    margin-top: 15px;
    background-color: #1ED760;
    text-align: center;
    border: 2px solid #020803;
    font-size: 25px;
    font-family: "Fredoka One";
    border-radius: 60px;
    width: 300px;
    height: 60px;
    outline: transparent;
    color: #000;
}

.login-btn:hover {
    background-color: #000;
    color: #1ED760;
}

.finish-game-modal {
    position: absolute;
    top: 30%;
    left: 30%;
    right: 30%;
    bottom: 30%;
    background-color: #64df75;
    border-radius: 20px;
    border: 2px solid black;
    outline: none;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
